section > #index_c {
  animation: move 0.7s;
}
section > #index_c > .row {
  position: relative;
}
section > #index_c > .row > .col-md-8 > .row #carouselExampleCaptions {
  border-radius: 15px;
  overflow: hidden;
  transition: transform 0.3s;
}
section > #index_c > .row > .col-md-8 > .row #carouselExampleCaptions .carousel-indicators {
  margin: 0;
}
section > #index_c > .row > .col-md-8 > .row #carouselExampleCaptions .carousel-item .carousel-caption {
  padding-bottom: 0.4rem;
}
section > #index_c > .row > .col-md-8 > .row #carouselExampleCaptions .carousel-item .carousel-caption h5,
section > #index_c > .row > .col-md-8 > .row #carouselExampleCaptions .carousel-item .carousel-caption p {
  color: #ffffff;
  margin-bottom: 0;
  font-size: 1rem;
  font-weight: bold;
}
section > #index_c > .row > .col-md-8 > .row #article {
  margin-top: 1rem;
  border-radius: 15px;
  overflow: hidden;
  transition: transform 0.3s;
  width: 50rem;
}
section > #index_c > .row > .col-md-8 > .row #article li {
  line-height: 1;
  background-color: #f8f9fa;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
section > #index_c > .row > .col-md-8 > .row #article li a {
  color: rgba(0, 0, 0, 0.5);
}
section > #index_c > .row > .col-md-4 .jia_info {
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  height: 15rem;
  font-family: 'Comic Sans MS';
  border-radius: 0.9rem;
  overflow: hidden;
  transition: transform 0.3s, backgroundColor 0.7s, color 1s, backgroundImage 0.7s;
}
section > #index_c > .row > .col-md-4 .jia_info h2 {
  font-weight: bold;
  font-size: 1.2rem;
}
section > #index_c > .row > .col-md-4 .jia_info .avatar {
  width: 5rem;
  height: 5rem;
  background-image: url('../../images/avatar.jpg');
  background-position-x: -3px;
  border-radius: 50%;
  overflow: hidden;
}
section > #index_c > .row > .col-md-4 .jia_info .text-center {
  line-height: 1;
  margin-bottom: 0;
}
section > #index_c > .row > .col-md-4 .card {
  margin-top: 1rem;
  border-radius: 15px;
  overflow: hidden;
  transition: transform 0.3s, color 1s;
  background-color: rgba(255, 255, 255, 0.5);
  border: none;
  color: #ffffff;
}
section > #index_c > .row > .col-md-4 .card .card-body {
  padding: 0.75rem 1.25rem;
}
section > #index_c > .row > .col-md-4 .card .card-body .card-text {
  margin-bottom: 0.75rem;
}
section > #index_c > .row > .col-md-4 .card .card-body .card-link {
  position: absolute;
  right: 1.75rem;
  color: #7c7c7d;
}
section > #index_c > .row > .col-md-4 .card .card-body .card-link:hover {
  text-decoration: underline;
}
section > #index_c > .row > .col-md-4 .right_btn {
  margin-top: 1rem;
  height: 3rem;
}
section > #index_c > .row > .col-md-4 .right_btn .left {
  background-color: rgba(255, 255, 255, 0.5);
  height: 3rem;
}
section > #index_c > .row > .col-md-4 .right_btn .left .btn_up,
section > #index_c > .row > .col-md-4 .right_btn .left .toggle {
  width: 50%;
  height: 3rem;
  line-height: 3rem;
  text-align: center;
}
section > #index_c > .row > .col-md-4 .right_btn .left .btn_up {
  float: left;
}
section > #index_c > .row > .col-md-4 .right_btn .left .toggle {
  float: right;
}
section > #index_c > .row > .col-md-4 .right_btn .right {
  height: 3rem;
}
section > #index_c > .row > .col-md-4 .right_btn .right .btn_hidden,
section > #index_c > .row > .col-md-4 .right_btn .right .toggle_hidden {
  width: 50%;
  height: 0;
  line-height: 3rem;
  text-align: center;
  overflow: hidden;
  background-color: rgba(255, 0, 0, 0.5);
  transition: height 0.5s;
}
section > #index_c > .row > .col-md-4 .right_btn .right .btn_hidden {
  float: left;
}
section > #index_c > .row > .col-md-4 .right_btn .right .toggle_hidden {
  float: right;
}
@keyframes move {
  0% {
    transform: translateX(100px);
  }
  100% {
    transform: translateX(0px);
  }
}
@media (max-width: 768px) {
  section {
    background-color: white !important;
    background-image: none !important;
  }
}
@media (min-width: 768px) {
  #index_c #carouselExampleCaptions:hover,
  #index_c .list-group:hover {
    transform: translateY(-10px);
  }
  #index_c .card:hover {
    transform: translateY(-10px);
    color: rgba(0, 0, 0, 0.7);
  }
  #index_c #right_block > .jia_info:hover {
    transform: translateY(-10px);
    background-image: url(../../images/info_bg.jpg);
    color: black;
  }
  #index_c #move:hover #name {
    transform: translateY(-3.1vw);
  }
  #index_c #inner1 .col-md-8 {
    padding-right: 0;
  }
  #index_c #inner2 .col-md-8 {
    padding-right: 0;
  }
}
