html {
  perspective: 1000px;
}
#case_c {
  animation: letf 0.5s;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
#case_c .item {
  width: 24.5%;
  border-radius: 15px;
  overflow: hidden;
  margin-bottom: 0.5rem;
  background-color: white;
  transition: transform 0.3s, filter 0.3s;
}
#case_c .item:not(:nth-of-type(4)) {
  margin-right: 0.666%;
}
#case_c .item .item_img img {
  width: 100%;
}
#case_c .item .item_content {
  padding: 0.7rem;
}
#case_c .item .item_content .item_title {
  font-size: inherit;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.85);
}
#case_c .item .item_content .item_introduce {
  text-decoration: none;
}
#case_c .item .item_content .item_introduce a {
  color: rgba(0, 0, 0, 0.5);
}
#case_c .item.zFloat {
  z-index: 9999 ;
  transform: translateY(-3px);
}
.item.sink {
  filter: blur(5px);
  transform: translateZ(-60px) translateX(-15px);
}
@media (max-width: 991px) {
  .item_content {
    height: 4rem;
  }
  .item_content .item_introduce {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
@keyframes letf {
  0% {
    transform: translateX(-100px);
  }
  100% {
    transform: translateX(0px);
  }
}
