section #article_c {
  animation: letf 0.5s;
}
section #article_c .list-group {
  border-radius: 15px;
  overflow: hidden;
}
section #article_c .list-group:not(:nth-of-type(1)) {
  margin-top: 1rem;
}
section #article_c .list-group li {
  background-color: #f8f9fa;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 1;
}
section #article_c .list-group li h3 {
  margin-bottom: 0;
  line-height: 1;
  font-size: inherit;
  font-weight: bold;
}
section #article_c .list-group li a {
  color: rgba(0, 0, 0, 0.5);
}
@keyframes letf {
  0% {
    transform: translateX(-100px);
  }
  100% {
    transform: translateX(0px);
  }
}
